// ** React Imports
import { Link, useNavigate, useParams } from 'react-router-dom'
import classnames from 'classnames'
// ** Third Party Components
import RoleCards from './RoleCards'
import Select from 'react-select'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import axios from '@src/configs/axios/axiosConfig'

import { updateUser, getUser } from '../store/index'
import { Row, Col, Card, Label, Button, CardBody, Input, FormFeedback } from 'reactstrap'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Styles
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/base/pages/app-invoice.scss'
import { useEffect, useState, Fragment } from 'react'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import { useDispatch, useSelector } from 'react-redux'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
const EditCard = () => {

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const { id } = useParams()
  // ** States
  const schema = yup.object().shape({
    updatedBy: yup.string().default(userId),
    organizationId: yup.string().default(activeOrgId),
    firstName: yup.string().required("Please Enter a First Name"),
    lastName: yup.string().required("Please Enter a Last Name"),
    name: yup.string().default(''),
    contact: yup.string().required("Please Enter a Conatct No").max(10).min(10, "Invalid Contact No"),
    email: yup.string().email("Please Enter valid Email").required("Please Enter valid Email"),
    designationId: yup.string().required("Please Select Designation"),
    roleId: yup.string().required("Please Select Role"),
    departmentId: yup.string().required("Please Select Department")
  })

  const { handleSubmit, control, formState: { errors }, reset } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })
  const store = useSelector(state => state.team)
  const [departmentOptions, setDepartmentOptions] = useState([])
  const [designationOptions, setDesignationOptions] = useState([])
  const [rolesOptions, setRolesOptions] = useState([])
  const [permissions, setPermissions] = useState([])
  const [roleId, setRoleId] = useState('')

  const getRolePermissions = () => {
    axios.post('/rolepermissions/list', { roleId }).
      then((res) => {
        setPermissions(res.data.rolepermission.permissions.split(','))
      }).catch(() => { })
  }

  const getDesignation = () => {
    axios.post('/designations/dropdown').
      then((res) => {
        setDesignationOptions(res.data.designations)
      }).catch(() => { })
  }

  const getDepartment = () => {
    axios.post('/departments/dropdown')
      .then((res) => {
        setDepartmentOptions(res.data.departments)
      }).catch(() => { })
  }

  const getRoles = () => {
    axios.post('/roles/dropdown').then((res) => { setRolesOptions(res.data.roles) }).catch(() => { })
  }

  useEffect(async () => {
    await getDesignation()
    await getDepartment()
    await getRoles()

    await dispatch(getUser(id))

  }, [])

  useEffect(() => {
    if (roleId !== '') {
      getRolePermissions()
    }
  }, [roleId])

  useEffect(async () => {
    if (store.selectedUser !== null) {
      const data = store.selectedUser
      const fullname = data.name !== null ? data.name.split(' ') : ''
      reset({
        updatedBy: userId,
        organizationId: activeOrgId,
        firstName: fullname[0] || '',
        lastName: fullname[1] || '',
        name: data.name || '',
        id,
        contact: data.contactno || '',
        email: data.email,
        designationId: data.designationid || '',
        roleId: data.roleid || '',
        departmentId: data.departmentid || ''
      })
      setRoleId(data.roleid)
    }
  }, [store.selectedUser])

  const onSubmit = async data => {
    setBlock(true)
    data['name'] = `${control._formValues.firstName} ${control._formValues.lastName}`
    await dispatch(updateUser(data))
    navigate(`/team/view/${id}`)
  }

  return (
    <UILoader blocking={block} loader={<Loader />}>
      <form onSubmit={handleSubmit(onSubmit)} >
        <Card className='invoice-preview-card'>
          {/* Header */}

          {Object.keys(errors).map((obj, k) => {
            return <FormFeedback key={k}> {errors[obj]?.message}</FormFeedback>
          })}

          <CardBody className='pb-0'>
            <Row>
              <Col md='6' className='mb-1'>
                <Row className='mb-1'>
                  <Label sm='3' size='lg' className='form-label required' for='firstName'>
                    First Name
                  </Label>
                  <Col sm='9'>
                    <Controller
                      id='firstName'
                      name='firstName'
                      control={control}
                      render={({ field }) => <Input invalid={errors.firstName && true} {...field} />}
                    />
                    {errors.firstName && <FormFeedback>{errors.firstName.message}</FormFeedback>}
                  </Col>
                </Row>
              </Col>
              <Col md='6' className='mb-1'>
                <Row className='mb-1'>
                  <Label sm='3' size='lg' className='form-label required' for='lastName'>
                    Last Name
                  </Label>
                  <Col sm='9'>
                    <Controller
                      control={control}
                      id='lastName'
                      name='lastName'
                      render={({ field }) => (
                        <Input type='text' invalid={errors.lastName && true} {...field} />
                      )}
                    />
                    {errors.lastName && <FormFeedback>{errors.lastName.message}</FormFeedback>}
                  </Col>
                </Row>
              </Col>
            </Row>
            <Row>
              <Col md='6' className='mb-1'>
                <Row className='mb-1'>
                  <Label sm='3' size='lg' className='form-label required' for='contact'>
                    Conatct No
                  </Label>
                  <Col sm='9'>
                    <Controller
                      id='contact'
                      name='contact'
                      control={control}
                      render={({ field }) => <Input invalid={errors.contact && true} {...field} />}
                    />
                    {errors.contact && <FormFeedback>{errors.contact.message}</FormFeedback>}
                  </Col>
                </Row>
              </Col>
              <Col md='6' className='mb-1'>
                <Row className='mb-1'>
                  <Label sm='3' size='lg' className='form-label required' for='email'>
                    Email
                  </Label>
                  <Col sm='9'>
                    <Controller
                      control={control}
                      id='email'
                      name='email'
                      render={({ field }) => (
                        <Input type='email' invalid={errors.email && true} {...field} disabled />
                      )}
                    />
                    {errors.email && <FormFeedback>{errors.email.message}</FormFeedback>}
                  </Col>
                </Row>
              </Col>
            </Row>

            <Row>
              <Col md='6' className='mb-1'>
                <Row className='mb-1'>
                  <Label sm='3' size='lg' className='form-label required' for='designationId'>
                    Designation
                  </Label>
                  <Col sm='9'>
                    <Controller
                      control={control}
                      name="designationId"
                      id="designationId"
                      render={({ field, ref }) => (
                        <Select
                          {...field}
                          inputRef={ref}
                          className={classnames('react-select', { 'is-invalid': errors.designationId })}
                          classNamePrefix='select'
                          options={designationOptions}
                          value={designationOptions.find(c => { return c.id === field.value })}
                          onChange={val => field.onChange(val.id)}
                          getOptionLabel={(option) => option.name}
                          getOptionValue={(option) => option.id}
                        />
                      )}

                    />
                    {errors.designationId && <FormFeedback className='text-danger'>{errors.designationId?.message}</FormFeedback>}
                  </Col>
                </Row>
              </Col>

              <Col md='6' className='mb-1'>
                <Row className='mb-1'>
                  <Label sm='3' size='lg' className='form-label required' for='roleId'>
                    Role
                  </Label>
                  <Col sm='9'>
                    <Controller
                      control={control}
                      name="roleId"
                      id="roleId"
                      render={({ field, ref }) => (
                        <Select
                          {...field}
                          inputRef={ref}
                          className={classnames('react-select', { 'is-invalid': errors.roleId })}
                          classNamePrefix='select'
                          options={rolesOptions}
                          value={rolesOptions.find(c => { return c.id === field.value })}
                          onChange={val => { setRoleId(val.id); return field.onChange(val.id) }}
                          getOptionLabel={(option) => option.name}
                          getOptionValue={(option) => option.id}
                        />
                      )}

                    />
                    {errors.roleId && <FormFeedback className='text-danger'>{errors.roleId?.message}</FormFeedback>}
                  </Col>
                </Row>
              </Col>
            </Row>
            <Row>
              <Col md='6' className='mb-1'>
                <Row className='mb-1'>
                  <Label sm='3' size='lg' className='form-label required' for='departmentId'>
                    Department
                  </Label>
                  <Col sm='9'>
                    <Controller
                      control={control}
                      name="departmentId"
                      id="departmentId"
                      render={({ field, ref }) => (
                        <Select
                          {...field}
                          inputRef={ref}
                          className={classnames('react-select', { 'is-invalid': errors.departmentId })}
                          classNamePrefix='select'
                          options={departmentOptions}
                          value={departmentOptions.find(c => { return c.id === field.value })}
                          onChange={val => field.onChange(val.id)}
                          getOptionLabel={(option) => option.name}
                          getOptionValue={(option) => option.id}
                        />
                      )}

                    />
                    {errors.departmentId && <FormFeedback className='text-danger'>{errors.departmentId?.message}</FormFeedback>}
                  </Col>
                </Row>
              </Col>

            </Row>

          </CardBody>

          {permissions.length > 0 &&
            <CardBody className='invoice-padding'>
              <RoleCards permissions={permissions} />
            </CardBody>
          }
        </Card>
        <Card>
          <CardBody>
            <div className='modal-footer border-0'>
              <Button color='warning' outline tag={Link} to='/team/list'>
                Cancel
              </Button>
              <Button color='primary' type="submit" >
                Save
              </Button>
            </div>
          </CardBody>
        </Card>
      </form>
    </UILoader>
  )
}

export default EditCard
